<template>
    <div class="content-container">
        <el-form ref="ruleForm" :model="ruleForm" :rules="rules" label-width="300px" class="table">
            <el-form-item class="el-form-item__content" :label="item.cnName"
                          v-for="(item) of ruleForm.options" :key="item.ckey" :prop="item.ckey">
                <el-input v-model="ruleForm[item.ckey]" v-if="item.type==3" maxlength="80"></el-input>
                <el-input-number v-model="ruleForm[item.ckey]" :min="item.ckey=='OPTION_MAX_CEILING'?2:1" :max="9000000"
                                 v-if="item.type==1" :precision="0"></el-input-number>
                <el-switch v-model="ruleForm[item.ckey]" active-value="true" inactive-value="false"
                           v-if="item.type==2"></el-switch>
            </el-form-item>

            <el-form-item>
                <el-button type="primary" @click="submitForm('ruleForm')" icon="el-icon-news">保存</el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>

    export default {
        data() {
            return {
                ruleForm: {options: []},
                vo: {
                    ckey: '',
                    remarks: '',
                    cval: '',
                    showType: '',
                },
                rules: {
                }
            };
        },
        mounted() {
            this.axios.get("/api/console/config/get").then((res) => {
                if (res.data.state == 0) {
                    const initFormValue = res.data.data.reduce((preItem, nextItem) => {
                        return {
                            ...preItem,
                            [nextItem.ckey]: nextItem.cval
                        }
                    }, {})
                    this.ruleForm = {...initFormValue}
                    this.ruleForm.options = [...res.data.data];
                }
            }).catch((response) => {
                console.log(response);
            })
        },
        methods: {
            submitForm(formName) {
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        const ckeys = [];
                        const values = [];
                        const remarks = [];
                        const showType = [];
                        this.ruleForm.options.forEach((item) => {
                            ckeys.push(item.ckey);
                            values.push(this.ruleForm[item.ckey]);
                            remarks.push(item.remarks);
                            showType.push(item.showType);
                        });
                        this.vo.ckey = ckeys.join(',');
                        this.vo.cval = values.join(',');
                        this.vo.remarks = remarks.join(',');
                        this.vo.showType = showType.join(',');
                        this.axios.post('/api/console/config/save',
                            this.vo
                        ).then((res) => {
                            const message = res.data.message;
                            const state = res.data.state;
                            this.$message({
                                message,
                                type: state === 0 ? 'success' : 'error',
                            });
                        }).catch((response) => {
                            console.log(response);
                        })
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            }
        },
    };
</script>

<style scoped lang="less">
    .content-container {
        padding-left: 20px;
        padding-right: 20px;
    }

</style>
